@media (max-width: 576px) {
  .modal {
    padding: 40px 20px;
  }
}
.modal__box {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 40px;
}
.modal__icon {
  display: flex;
  max-width: 50%;
  width: 100%;
}
.modal__close {
  position: absolute;
  top: max(20px, 20rem);
  right: max(20px, 20rem);
  color: rgba(var(--cl-a), 1);
  transition: color 0.4s;
}
.modal__close svg {
  fill: currentColor;
  width: clamp(20px, 3.333vw, 35rem);
  height: clamp(20px, 3.333vw, 35rem);
}
.modal__close:hover {
  color: rgba(var(--cl-d), 1);
}
.modal__text {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: max(25px, 25rem);
  color: rgba(var(--cl-a), 1);
  font-family: var(--font-family);
  font-style: normal;
}
.modal__title {
  font-size: clamp(22px, 2.5vw, 30rem);
  font-weight: 700;
  line-height: 120%;
}
.modal__text {
  font-size: clamp(18px, 1.667vw, 20rem);
  font-weight: 500;
}